<template>
	<div class="informationPage">
		<div class="informationPage_head">
			<div class="informationPage_head_box">
				<p class="informationPage_head_title1">
					<i class="icon_bell"></i>
					<span>标题文案标题文案标题文案标</span>
				</p>
				<p class="informationPage_head_title2">
					<i class="icon_click"></i>
					<span v-if="getInformationConfData.readCount - $store.state.infoCount <= 0">已获得奖励</span>
					<span v-else>点击 {{ getInformationConfData.readCount - $store.state.infoCount < 0 ? 0 : getInformationConfData.readCount - $store.state.infoCount }} 次内容即可领取奖励</span>
				</p>
			</div>
			<p :class="getInformationConfData.readCount - $store.state.infoCount > 0?'informationPage_head_btn':'informationPage_head_btn act'" @click="goBack">继续赚金币 ></p>
		</div>
		<ul class="information_list">
			<li v-for="(item,index) in informationList" :key="item.content_id" @click="toInFo(index)">
				<p class="title">{{ item.title }}</p>
				<div class="img_list">
					<div class="img_list_box" v-for="(items,index) in item.list_images" :key="items.surl" v-show="index == 0">
						<img :src="items.img_url" alt="">
					</div>
				</div>
				<p class="source">{{ item.source+' '+item.read_count+'阅读' }}</p>
				<div class="infobanner_act" v-if="(index+item.informationCount) % sum1 == 0">
					<div class="infobanner" v-for="(items,ind) in item.adCount"></div>
				</div>
			</li>
		</ul>
		<!-- <i class="icon_withdrawal"></i> -->
	</div>
</template>

<script>
	var infoView;
	import dsbridge from '../assets/js/dsbridge.js';
	export default {
		name:'informationPage',
		data(){
			return {
				informationList:[],
				getInformationConfData:[],
				roles:{},
				watchConut:0,
				ScrollFlag:true,
				sum1:1,
				adList:[],
			}
		},
		watch:{
			adList(newData,oldData){
				let _this = this;
				for(var o in newData){
					var dom = document.getElementsByClassName('infobanner')[o];
					_this.showNativeExpressAd(newData[o],dom);
				}
			}
		},
		mounted() {
			let _this = this;
			var jsonObj = localStorage.getItem("obj");
			_this.obj = JSON.parse(jsonObj);
			_this.infoList(10,1);
			window.addEventListener('scroll', _this.handleScroll, true);
			
		},
		methods:{
			handleScroll(){
				let _this = this;
				var scrollTop = document.documentElement.scrollTop || document.body.scrollTop; //变量windowHeight是可视区的高度
				var windowHeight = document.documentElement.clientHeight || document.body.clientHeight; //变量scrollHeight是滚动条的总高度
				var scrollHeight = document.documentElement.scrollHeight || document.body.scrollHeight; //滚动条到底部的条件
				if(Number(scrollTop)+Number(windowHeight) > Number(scrollHeight)-200){
					if(_this.ScrollFlag){
						_this.ScrollFlag = false;
						console.log('加载。。。');
						_this.infoList(10,2);
					}
					
				}else{
					if(!_this.ScrollFlag){
						_this.ScrollFlag = true;
						console.log('离开。。。');
					}
				}
			},
			infoList(count,operation){
				let _this = this;
				var exclusiveObj = {
					count:count,//加载条数
					operation:operation,//客户端获取数据的行为；1是初始化，2是加载更多，3是刷新
					scenario:'0x010100',//频道
				};
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
				_this.$axios({
				  method: 'post',
				  url: '/info/list',
				  data:redata,
				}).then(function(res) {
					console.log(res);
				  if(res.data.code === 200){
					var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
					console.log(reData);
					_this.informationList = _this.informationList.concat(reData);
					if(operation == 1){
						_this.getInformationConf();
					}else if(operation == 2){
						var newArr1 = [];
						var newArr2 = [];
						var randomNum = Math.random()*100;
						var i = Math.floor(randomNum) % _this.getInformationConfData.informationStrategy.length;
						var slotIds = _this.getInformationConfData.informationStrategy[i].slotIds;
						for(var ss = 0;ss<_this.getInformationConfData.informationStrategy[i].roles.length;ss++){
							newArr1.push(Number(_this.getInformationConfData.informationStrategy[i].roles[ss].informationCount));
							newArr2.push(Number(_this.getInformationConfData.informationStrategy[i].roles[ss].adCount));
						}
						var sum1 = eval(newArr1.join('+'));
						_this.sum1 = sum1;
						var sum2 = eval(newArr2.join('+'));
						var arr1 = _this.secPageRowFormat(newArr1);
						var arr2 = _this.secPageRowFormat(newArr2);
						for(var ww in _this.informationList){
							_this.informationList[ww].informationCount = arr1[ww % sum1];
							_this.informationList[ww].adCount = arr2[ww % sum2];
							console.log('informationCount===',arr1[ww % sum1]);
							console.log('adCount===',arr2[ww % sum2]);
						}
						var counts = 0;
						for(var c in _this.informationList){
							counts += _this.informationList[c].adCount;
						}
						var shang = parseInt(counts/slotIds.length);
						var yu = counts % slotIds.length;
						for(var k=0;k< slotIds.length;k++){
							if(k<yu){
								_this.loadNativeExpressAd(slotIds[k],shang+1);
							}else{
								_this.loadNativeExpressAd(slotIds[k],shang);
							}
						}
					}
					
				  }
				});
			},
			getInformationConf(){
				let _this = this;
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
				_this.$axios({
				  method: 'post',
				  url: '/getInformationConf',
				  data:redata,
				}).then(function(res) {
				  if(res.data.code === 200){
					var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
					console.log(reData);
					_this.getInformationConfData = reData;
					_this.$store.commit('postRedRewardCount',reData.redRewardCount);
					_this.$store.commit('postRedRewardAmount',reData.redRewardAmount);
					var newArr1 = [];
					var newArr2 = [];
					var randomNum = Math.random()*100;
					var i = Math.floor(randomNum) % reData.informationStrategy.length;
					var slotIds = reData.informationStrategy[i].slotIds;
					for(var ss = 0;ss<reData.informationStrategy[i].roles.length;ss++){
						newArr1.push(Number(reData.informationStrategy[i].roles[ss].informationCount));
						newArr2.push(Number(reData.informationStrategy[i].roles[ss].adCount));
					}
					var sum1 = eval(newArr1.join('+'));
					var sum2 = eval(newArr2.join('+'));
					var arr1 = _this.secPageRowFormat(newArr1);
					var arr2 = _this.secPageRowFormat(newArr2);
					_this.sum1 = sum1;
					for(var ww in _this.informationList){
						_this.informationList[ww].informationCount = arr1[ww % sum1];
						_this.informationList[ww].adCount = arr2[ww % sum2];
					}
					var counts = 0;
					for(var c in _this.informationList){
						counts += _this.informationList[c].adCount;
					}
					var shang = parseInt(counts/slotIds.length);
					var yu = counts % slotIds.length;
					for(var k=0;k< slotIds.length;k++){
						if(k<yu){
							_this.loadNativeExpressAd(slotIds[k],shang+1);
						}else{
							_this.loadNativeExpressAd(slotIds[k],shang);
						}
					}
					
				  }
				});
			},
			secPageRowFormat(list){
				var result = {};
				var total = 0;
				var row_fowmat_list = list;
				for(var i=0;i<row_fowmat_list.length;i++)
				{
				    var curr_col_cnt = row_fowmat_list[i];
				    for(var j=0;j<curr_col_cnt;j++){
				        result[total+j] = curr_col_cnt;
				    }
				    total = total + curr_col_cnt;
				}
				return result;
			},
			toInFo(i){
				let _this = this;
				for(var sss in _this.adList){
					_this.destroyNativeExpressAd(_this.adList[sss]);
				}
				setTimeout(function(){
					_this.$store.commit('clickArticle',1);
					console.log(_this.$store.state.infoCount);
					_this.$router.push({path:'/infomationDetails',query:{item:_this.informationList[i]}})
				},200)
				
			},
			goBack(){
				let _this = this;
				if(_this.getInformationConfData.readCount - _this.$store.state.infoCount > 0){
					_this.$toast('再需要浏览'+(_this.getInformationConfData.readCount - _this.$store.state.infoCount)+'次内容就可以领取奖励了哦～');
				}else{
					_this.$router.back();
				}
			},
			loadNativeExpressAd(codeId,count){
				let _this = this;
				var li = document.getElementsByClassName('information_list')[0].children[0];
				var w = li.getBoundingClientRect().width;
				var h = li.getBoundingClientRect().height;
				var param = { code_id: codeId,count:count,w:w,h:h};
				dsBridge.call("lyh5x.fad.nativeexpress.load", param,_this.neli);
			},
			renderNativeExpressAd(view){
				let _this = this;
				var param = { view:view};
				dsBridge.call("lyh5x.fad.nativeexpress.render", param);
			},
			showNativeExpressAd(view,dom){
				var w = dom.getBoundingClientRect().width*window.devicePixelRatio;
				var h = dom.getBoundingClientRect().height*window.devicePixelRatio;
				var x = 0;
				var y = dom.offsetTop*window.devicePixelRatio;
				var param = { view:view,w:w,h:h,x:x,y:y,fixedOn:false};
				dsBridge.call("lyh5x.fad.nativeexpress.show", param);
			},
			destroyNativeExpressAd(view){
				var param = { ad:"" ,view:view,views:""};
				dsBridge.call("lyh5x.fad.nativeexpress.destroy", param);
			},
			neli(ret) {
				let _this = this;
			      if (ret.event == "onError") {
			          console.log('onError====',ret.msg);
			       } else if (ret.event == "onAdLoad") {
			          console.log('onAdLoad====',ret.msg);
			          for(var q in ret.views){
			          	_this.renderNativeExpressAd(ret.views[q]);
			          }
			       } else if (ret.event == "onRenderSuccess") {
			           console.log('onRenderSuccess====',ret.view);
			           _this.adList.push(ret.view);
					   console.log(_this.adList);
			       } else if (ret.event == "onRenderFail") {
					   console.log('onRenderFail====',ret.msg);
			       } else if (ret.event == "onAdShow") {
						console.log('onAdShow=====',ret);
			       } else if (ret.event == "onAdClicked") {
			         var id = ret.id;
			       } else if (ret.event == "onAdClosed") {
			         var id = ret.id;
			       } else if (ret.event == "onAdClosed") {
			         var id = ret.id;
			       }
			}
		},
		beforeDestroy() {
			let _this = this;
			window.removeEventListener("scroll", _this.handleScroll, true);
		}
	}
</script>
<style scoped>
	.informationPage{
		width: 10.8rem;
		min-height: 100vh;
		padding-bottom:0.5rem;
		padding-top:2.51rem;
	}
	.informationPage_head{
		width: 100%;
		height: 2.51rem;
		background-image: url(../static/img/img_infoPage.jpg);
		background-repeat: no-repeat;
		background-size: 100% 100%;
		display: flex;
		justify-content: space-between;
		align-items:center;
		box-sizing: border-box;
		padding:var(--barHeight) 0.44rem 0 0.44rem;
		position: fixed;
		top:0;
		left:0;
	}
	.informationPage_head_box{
		width: 6.8rem;
		height: 100%;
		
	}
	.informationPage_head_title1{
		font-size: 0.4rem;
		color:#fff;
		line-height: 0.78rem;
	}
	.informationPage_head_title2{
		font-size: 0.4rem;
		color:#ffeb7a;
	}
	.informationPage_head_box i{
		display: inline-block;
		vertical-align: middle;
		background-repeat: no-repeat;
		background-size: 100%;
	}
	.informationPage_head_box span{
		display: inline-block;
		vertical-align: middle;
	}
	.informationPage_head_title1 .icon_bell{
		width: 0.33rem;
		height: 0.4rem;
		background-image: url(../static/img/icon_bell.png);
		
	}
	.informationPage_head_title2 .icon_click{
		width: 0.5rem;
		height: 0.65rem;
		background-image: url(../static/img/icon_click.png);
	}
	.informationPage_head_btn{
		width: 2.9rem;
		height: 0.76rem;
		border-radius: 0.38rem;
		background-image: linear-gradient(to bottom,#f1f1f1,#f1f1f1);
		color:#999;
		text-align: center;
		line-height: 0.76rem;
		font-size: 0.35rem;
	}
	.informationPage_head_btn.act{
		background-image: linear-gradient(to bottom,#fde7b3,#ffeb7a);
		color:#bd5400;
	}
	.information_list{
		width: 10.8rem;
		box-sizing: border-box;
		padding:0 0.44rem;
	}
	.information_list li{
		width: 100%;
		margin:0.2rem 0 0.7rem 0;
	}
	.information_list .title{
		font-size: 0.46rem;
		color:#000;
		line-height: 0.65rem;
		margin-bottom:0.1rem;
	}
	.information_list .img_list{
		width: 100%;
		border-radius: 0.2rem;
		overflow: hidden;
		display: flex;
		justify-content: space-between;
		align-items:center;
	}
	.information_list .img_list .img_list_box{
		width: 100%;
		height: 5.5rem;
	}
	.information_list .img_list .img_list_box img{
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	.information_list .source{
		font-size: 0.3rem;
		color:#8a8a8a;
		margin-top:0.16rem;
	}
	.icon_withdrawal{
		display: inline-block;
		width: 2rem;
		height:2rem;
		background-image: url(../static/img/icon_withdrawal.png);
		background-repeat: no-repeat;
		background-size: 100%;
		position: fixed;
		bottom:2rem;
		left:0;
	}
	.infobanner_act{
		width: 100%;
	}
	.infobanner_act .infobanner{
		width: 100%;
		height:var(--bannerPadding);
	}
	
</style>
